@mixin d {
  @media only screen and (max-width: "1199px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@include font(SFUIDisplayRegular, SFUIDisplayRegular);
@mixin f {
  font-family: 'SFUIDisplayRegular', serif; }

@include font(SFUIDisplayUltralight, SFUIDisplayUltralight);
@mixin f1 {
  font-family: 'SFUIDisplayUltralight', serif; }

@include font(SFUIDisplayLight, SFUIDisplayLight);
@mixin f2 {
  font-family: 'SFUIDisplayLight', serif; }

@include font(SFUIDisplayThin, SFUIDisplayThin);
@mixin f3 {
  font-family: 'SFUIDisplayThin', serif; }

@include font(SFUIDisplayMedium, SFUIDisplayMedium);
@mixin f5 {
  font-family: 'SFUIDisplayMedium', serif; }

@include font(SFUIDisplaySemibold, SFUIDisplaySemibold);
@mixin f6 {
  font-family: 'SFUIDisplaySemibold', serif; }

@include font(SFUIDisplayBold, SFUIDisplayBold);
@mixin f7 {
  font-family: 'SFUIDisplayBold', serif; }

@include font(SFUIDisplayHeavy, SFUIDisplayHeavy);
@mixin f8 {
  font-family: 'SFUIDisplayHeavy', serif; }

@include font(SFUIDisplayBlack, SFUIDisplayBlack);
@mixin f9 {
  font-family: 'SFUIDisplayBlack', serif; }

@include font(SFUIDisplayBlack, SFUIDisplayBlack);
@mixin f9 {
  font-family: 'SFUIDisplayBlack', serif; }

@include font(GothamBlack, GothamBlack);
@mixin fg {
  font-family: 'GothamBlack', serif; }

@include font(MarkProHeavy, MarkProHeavy);
@mixin fm {
  font-family: 'MarkProHeavy', serif; }

$black: #000;
$white: #fff;
$blue: #75e1ff;
$green: #9EF19D;
$purple: #501FFF;

body {
  min-width: 320px;
  @include f;
  font-size: 16px;
  line-height: 1.45;
  letter-spacing: 0.03em;
  &.no-scroll {
    overflow: hidden; } }
body,
button,
input,
textarea,
select {}

button {
  background: none; }

a {
 text-decoration: none;
 color: inherit;
 &:hover {
   cursor: pointer; } }


